import axios from 'axios'
import utils from './utils'
// axios
// axios.defaults.timeout = 30000; //30s
// axios.defaults.headers.post['Content-Type'] = 'application/json;charset=UTF-8'

//
let  timeStamp = Date.parse (new Date ()) / 1000;
  function getSign(){
    let util =   utils.gainPhone({
      appKey: '7863228592',
      timestamp: timeStamp,
      accessToken:sessionStorage.getItem('accessToken'),
      accountID:sessionStorage.getItem('accountID'),
      })
    return util
  }
  console.log(timeStamp)
  console.log(getSign())
  //
  let headers = {
    'Content-Type':'application/json;charset=UTF-8',
    appKey: '7863228592',
    timestamp: timeStamp,
    accessToken:sessionStorage.getItem('accessToken'),
    accountID:sessionStorage.getItem('accountID'),
    sign: getSign()
  }
// axios
const service = axios.create({
  headers:headers,
  // baseURL: process.env.BASE_API, // `baseURL`  `url`  `url`  URL
  timeout: 10000
})

// 
service.interceptors.response.use(
  response => {
    // dataAxios  axios  data
    const dataAxios = response.data
    // 
    const { ERRORCODE } = dataAxios
    //  ERRORCODE 
    if (ERRORCODE === undefined) {
      //  ERRORCODE 
      return dataAxios
    } else {
      //  ERRORCODE  
      switch (ERRORCODE) {
        case 0:
          // [  ] ERRORCODE === 0 
          return dataAxios.RESULT
        case '0':
          // [  ] ERRORCODE === '0' 
          return dataAxios.RESULT
        case 302:
          // 302
          window.location.href = dataAxios.URL
          // 
          break
        default:
          //  ERRORCODE
          if (process.env.NODE_ENV === 'development') {
            console.log('>>>>>> Error >>>>>>')
            console.log(dataAxios, 11111)
          }
          return Promise.reject(dataAxios)
      }
    }
  },
  error => {
    if (error && error.response) {
      switch (error.response.status) {
        case 400:
          error.message = ''
          break
        case 401:
          error.message = ''
          break
        case 403:
          error.message = ''
          break
        case 404:
          error.message = `: ${error.response.config.url}`
          break
        case 408:
          error.message = ''
          break
        case 500:
          error.message = ''
          break
        case 501:
          error.message = ''
          break
        case 502:
          error.message = ''
          break
        case 503:
          error.message = ''
          break
        case 504:
          error.message = ''
          break
        case 505:
          error.message = 'HTTP'
          break
        default:
          break
      }
    }
    console.log(error.message)
    return Promise.reject(error)
  }
)

export default (url = '', data = {}, type = 'get') => {
  if (type.toLowerCase() === 'get') {
    return new Promise((resolve, reject) => {
      service
        .get(url, { params: data })
        .then(resp => {
          resolve(resp)
          // console.log(resp)
          // if (resp.data.ERRORCODE === 302) {
          //   window.location.href = resp.data.URL
          // } else {
          // resolve(resp.data)
          // }
        })
        .catch(e => {
          reject(e)
        })
    })
  }
  if (type.toLowerCase() === 'post' || type.toLowerCase() === 'put') {
    return new Promise((resolve, reject) => {
      service
        .post(url, data)
        .then(resp => {
          resolve(resp)
        })
        .catch(e => {
          resolve({ status: 'error', errCode: e.ERRORCODE, msg: e.RESULT.msg })
        })
    })
  }
}